import React from 'react'
import { FormattedMessage } from 'react-intl'
import { Link } from 'react-router-dom';
import './cookieconsent.min.css'
import './cookieconsent.min.js' 
import { NavLink, Collapse, Navbar, NavbarToggler, NavbarBrand, Nav, NavItem, UncontrolledDropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap'
import './index.css'


const getLink = (langKey, uri) => {
  if (langKey.toLowerCase() === 'en') {
    return `/${uri}`
  }
  return `/${langKey}/${uri}`
}

class NavbarTop extends React.Component {
  state = {
    isOpen: false,
  }

  listenScrollEvent = e => {
    if (window.scrollY > 40) {
      switch (location.pathname) {
        case "/aressuite":
        let mainsuite = document.querySelector('.mainsuite');
        let mainsuitecompStyles = window.getComputedStyle(mainsuite);
        this.setState({background: mainsuitecompStyles.backgroundImage})
          break;
        case "/aresarena":
        let mainarena = document.querySelector('.mainarena');
        let mainarenacompStyles = window.getComputedStyle(mainarena);
        this.setState({background: mainarenacompStyles.backgroundImage})
          break;
        case "/aresconnect":
        let mainconnect = document.querySelector('.mainconnect');
        let mainconnectcompStyles = window.getComputedStyle(mainconnect);
        this.setState({background: mainconnectcompStyles.backgroundImage})
          break;    
        case "/aressuitedocuments":
        let aressuitedocuments = document.querySelector('.mainsuite');
        let aressuitedocumentscompStyles = window.getComputedStyle(aressuitedocuments);
        this.setState({background: aressuitedocumentscompStyles.backgroundImage})
        break;
        case "/about":
        let about = document.querySelector('.mainabout');
        let aboutcompStyles = window.getComputedStyle(about);
        this.setState({background: aboutcompStyles.backgroundImage})
        break;
        case "/wallet":
        let wallet = document.querySelector('.mainwallet');
        let walletcompStyles = window.getComputedStyle(wallet);
        this.setState({background: walletcompStyles.backgroundImage})
        break;
        default : 
        let main = document.querySelector('.main');
        let maincompStyles = window.getComputedStyle(main);
        this.setState({background: maincompStyles.backgroundImage})
        break;
       }

    } else {
      this.setState({background: ''})
    }
  }

  componentDidMount(){
    window.addEventListener('scroll', this.listenScrollEvent);
  }
  openNav = () => {
    this.setState({ isOpen: true })
  }

  closeNav = () => {
    this.setState({ isOpen: false })
  }

  toggle = () => {
    if (this.state.isOpen) {
      this.closeNav()
    } else {
      this.openNav()
    }
  }
  

  render() {
    const { langKey } = this.props
    return (
      <div id="page-top" className="menu-container main-arestech" style={{background: this.state.background}}>
        <Navbar className="ares-navbar container" color="light" light expand="md">
          <NavbarBrand href={getLink(langKey, '#page-top')}>
            <img className="navbar-logo" src="/logos/logo-1024-light.svg" alt="ARES" width="60" />
          </NavbarBrand>
          <NavbarToggler onClick={this.toggle} />
          <Collapse isOpen={this.state.isOpen} navbar>
            <Nav navbar>
              <NavItem>
                <NavLink>
                  <Link to={getLink(langKey, '#products')} onClick={this.closeNav}>
                  <FormattedMessage id="navigation.item.products" />
                  </Link>
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink>
                 <Link to={getLink(langKey, '#about-us')} onClick={this.closeNav}>
                  <FormattedMessage id="navigation.item.about.us" />
                  </Link>
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink>
                 <Link to={getLink(langKey, '#Whitepaper')} onClick={this.closeNav}>
                  <FormattedMessage id="navigation.item.whitepaper" />
                  </Link>
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink>
                  <Link to={getLink(langKey, '#join-us')} onClick={this.closeNav}>
                  <FormattedMessage id="navigation.item.join.us" />
                  </Link>
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink> 
                  <Link to={getLink(langKey, '#faqs')} onClick={this.closeNav}>
                  <FormattedMessage id="navigation.item.faqs" />
                  </Link>
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink> 
                  <Link to={getLink(langKey, '#token-info')} onClick={this.closeNav}>
                  <FormattedMessage id="navigation.item.token.info" />
                  </Link>
                </NavLink>
              </NavItem>
              <div className="language-switcher-container">
                <UncontrolledDropdown
                  className="language-switcher"
                  nav
                  inNavbar
                  onClick={e => {
                    e.stopPropagation()
                  }}
                >
                  <DropdownToggle nav caret style={{paddingRight : '0px'}}>
                    {langKey.toUpperCase()}
                  </DropdownToggle>
                  <DropdownMenu right>
                    <DropdownItem>
                      <a href="/">English</a>
                    </DropdownItem>
                    <DropdownItem>
                      <a href="/zh">中文(zh-cn)</a>
                    </DropdownItem>
                  </DropdownMenu>
                </UncontrolledDropdown>
              </div>
            </Nav>
          </Collapse>
        </Navbar>
      </div>
    )
  }
}

export default NavbarTop



